const calcCardRotate = (i: number) => {
  const middleIndex = Math.floor(player.value.ItemList.length / 2);
  const deg = (i - middleIndex) * 5.5;
  const y = Math.abs((i - middleIndex)) * Math.abs((i-middleIndex)) * 2;
